@import "./colors";

.header {
    position: relative;
    width: 100%;
    height: 730px;
}

.container {
    width: 92%;
    max-width: 1152px;
    padding: 0 16px;
    margin: auto;
}

.header_nav {
    padding-top: 20px;
    display:inline-block;   
}

.nav_logo {
    max-width: 100%;
    height: auto;
    display: block;
    margin-left: 23px;
}

.header_menu {
    margin-right: 14px;
    list-style: none;
}

.header_menu_item {
    padding: 0 33px;
    display:inline-block;   
}